.services-page {
  min-height: 100vh;
  padding: 4rem 0;
  background: var(--bg-primary);
  transition: background-color 0.3s;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.service-section {
  background: #fff;
  border-radius: 20px;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 250px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.service-left {
  background: #fff;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}

.service-illustration {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.service-illustration svg {
  max-width: 100%;
  height: auto;
}

.service-illustration img {
  max-width: 50%;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
}

.service-title-left {
  font-size: 2rem;
  font-weight: 900;
  color: #000;
  margin: 0;
  text-transform: uppercase;
  line-height: 1;
}


.service-right {
  background: #000;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}

.service-description {
  color: #fff;
  font-size: 0.9rem;
  line-height: 1.8;
  margin-top: 1rem;
}

.service-description p {
  margin: 0;
}

.service-divider {
  position: absolute;
  bottom: 2.2rem;
  left: 1.5rem;
  right: 1.5rem;
  height: 1px;
  background: #fff;
  opacity: 0.3;
}

.service-details-btn {
  background: #fff;
  color: #000;
  padding: 0.3rem 0.2rem;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.7rem;
  transition: all 0.3s;
  display: inline-block;
  width: 25%;
  text-align: center;
}

.service-details-btn:hover {
  background: #f0f0f0;
  transform: translateY(-2px);
}

@media (max-width: 968px) {
  .service-section {
    grid-template-columns: 1fr;
    min-height: auto;
  }

  .service-right {
    min-height: 150px;
  }

  .service-title-left {
    font-size: 1.5rem;
  }
}

@media (max-width: 768px) {
  .services-page {
    padding: 2rem 0;
  }

  .container {
    gap: 2rem;
  }

  .service-left {
    padding: 1rem;
  }

  .service-right {
    padding: 1rem;
  }

  .service-title-left {
    font-size: 1.25rem;
  }

  .service-illustration {
    margin-bottom: 0.5rem;
  }
}
